export default {
  data() {
    return {
      switchGameBoll: false,
    }
  },
  props: ['poolData'],
  methods: {
    playBollEve() {
      this.switchGameBoll = !this.switchGameBoll
    },
    childPlayEve() {
      if (this.poolData.balance < 10) {
        this.$message.info('Not enough balance')
        return
      }
      this.switchGameBoll = !this.switchGameBoll
      this.$emit('childPlayMethod')
    },
  },
}
